.cmp-method-list{

	.method-list{
		list-style-type: none;
	    margin: 0;
	    padding: 0;
	    width: 100%;
	}

	.method-list-item{
		width: 100%;
	    display: inline-block;
	    vertical-align:top;
	    margin-top: -1px;
		margin-left: -1px;

		input{ display: none; }

		input:checked + label{
			background-color: $brand-primary;
			color: white;
		}

		label{
		    border: 1px solid $gray-lighter;
	    	cursor: pointer;
			padding: .5em;
			margin-bottom: 0;
			width:100%;
			overflow: hidden;
		}

		.content,
		.icon{
			display: inline-block;
			vertical-align: middle;
		}

		.icon{
			width: 3.5rem;
			height: 3.5rem;
			
			img{
				width: 100%;
				height: auto;
			}
		}

		.content{
			width: calc( 100% - 3.5rem );
			padding-left: .5rem
		}
	}

	@include media-breakpoint-up(sm){

	}

	@include media-breakpoint-up(md){
		.method-list{
			display: flex;
			flex-flow: row wrap;
		}
		.method-list-item{
			width: 50%;
			display: flex;
			flex-flow: row nowrap;
		}
	}

	@include media-breakpoint-up(lg){

	}
}
